import React, { useEffect } from "react";
import { Grid } from "@material-ui/core";
import Loader from "../../components/Common/Loader";
import { useDispatch } from "react-redux";
import { requestProfileDetails } from "../../../redux/actions";
import { PayoutMethods } from "../../components/MyProfile/PayoutMethods";
import { IdentityVerification } from "../../components/MyProfile/IdentityVerification/IdentityVerification";
const BasicDetails = React.lazy(() =>
  import("../../components/MyProfile/BasicDetails")
);

const MyProfile = () => {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(requestProfileDetails());
    // eslint-disable-next-line
  }, []);
  return (
    <>
      <Grid container>
        <Grid item sm={12} md={6} lg={6} xs={12} className="card-container">
          <React.Suspense fallback={<Loader />}>
            <BasicDetails />
          </React.Suspense>
        </Grid>
        <Grid item sm={12} md={6} lg={6} xs={12}>
          <div className="card-container">
          <PayoutMethods /></div>
          <div className="card-container">
          <IdentityVerification /></div>
        </Grid>
      </Grid>
    </>
  );
};

export default MyProfile;
