import {
  Card,
  CardContent,
  CardHeader,
  Divider,
  Grid,
  Chip,
} from "@material-ui/core";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useLocation } from "react-router";
import * as qs from "query-string";
import { IRootState } from "../../../../interfaces";
import {
  toggleModals,
  requestPayoutMethods,
  deactivateFundAccount,
  activateFundAccount,
} from "../../../../redux/actions";
import AppButton from "../../Common/AppButton";
import AddPayoutMethod from "./AddPayoutMethod";
import Loader from "../../Common/Loader";
import card from "../../../assets/credit-card.svg";

export const PayoutMethods = () => {
  const { search }: any = useLocation();
  const dispatch = useDispatch();
  const { openAddModal, payoutMethods } = useSelector((state: IRootState) => ({
    openAddModal: state.modalReducer.showAddPaymentMethodModal,
    payoutMethods: state.paymentMethodsReducer,
  }));
  useEffect(() => {
    const query: any = qs.parse(search);    
    if (query.addPayout === 'true') {
      dispatch(toggleModals({ showAddPaymentMethodModal: true }))      
    }
    // eslint-disable-next-line
  },[search]);

  useEffect(() => {
    dispatch(requestPayoutMethods());
    // eslint-disable-next-line
  }, []);
  /**
   *
   * @param id
   * @param status
   */
  const activateDeactivateAccount = (id: string, status: boolean) => {
    if (status) {
      dispatch(deactivateFundAccount({ id }));
    } else {
      dispatch(activateFundAccount({ id }));
    }
  };
  return (
    <>
      <Card>
        <CardHeader
          title={
            <Grid container>
              <Grid item sm={8}>
                <div>Withdrawal(Payout) Methods</div>
              </Grid>
              <Grid item sm={4} className="text-right">
                <AppButton
                  text={"Add New Method"}
                  color="primary"
                  className="btn-primary no-margin"
                  onClick={() =>
                    dispatch(toggleModals({ showAddPaymentMethodModal: true }))
                  }
                />
              </Grid>
            </Grid>
          }
        />
        <Divider />
        <CardContent>
          {payoutMethods.isLoading ? (
            <Loader />
          ) : payoutMethods.list.length ? (
            payoutMethods.list.map((payout) => (
              <div className={"bank-card-details"} key={payout.id}>
                {payout.account_type === "vpa" ? (
                  <p>{payout.vpa.address}</p>
                ) : (
                  <>
                    <p>{payout.bank_account.bank_name}</p>
                    <p>{payout.bank_account.ifsc}</p>
                    <p>{payout.bank_account.name}</p>
                    <p>{payout.bank_account.account_number}</p>
                  </>
                )}
                {payout.active ? (
                  <Chip
                    label={"Active"}
                    color={"primary"}
                    size={"small"}
                    onClick={() => activateDeactivateAccount(payout.id, true)}
                  />
                ) : (
                  <Chip
                    label={"Deactive"}
                    color={"secondary"}
                    size={"small"}
                    onClick={() => activateDeactivateAccount(payout.id, false)}
                  />
                )}
              </div>
            ))
          ) : (
            <div className={"bank-card-details"}>
              <div className={"text-center"}>
                <div className="no-data">
                  <div className="no-data-icon">
                    <img src={card} alt={"money"} />
                  </div>
                  <p className="no-data-text">No payout method added!</p>
                </div>
              </div>
            </div>
          )}
        </CardContent>
      </Card>
      <AddPayoutMethod
        open={openAddModal}
        handleClose={() =>
          dispatch(toggleModals({ showAddPaymentMethodModal: false }))
        }
      />
    </>
  );
};
