import React from "react";
import { useDropzone } from "react-dropzone";
import { InputLabel, FormHelperText } from "@material-ui/core";
import {
  Delete,
} from "@material-ui/icons";
import { IAppDropzoneProps } from "../../../interfaces";

const AppDropZone = ({
  message,
  label,
  error,
  helperText,
  isClearable = false,
  onDelete,
  ...options
}: IAppDropzoneProps) => {
  const { getRootProps, getInputProps } = useDropzone(options);
  return (
    <div className="form-input drag-drop-wrapper">
      {label ? <InputLabel>{label}</InputLabel> : null}
      <br />
      <div
        className={`drag-drop-container ${error ? "error" : ""}`}
        {...getRootProps()}
      >
        <input {...getInputProps()} />
        <p>
          {message
            ? message
            : "Drag 'n' drop some files here, or click to select files"}
        </p>
      </div>
      {isClearable && !options.disabled ? <div className="delete-container" onClick={onDelete}>
        <Delete /></div> : null}
      {helperText ? <FormHelperText error>{helperText}</FormHelperText> : null}
    </div>
  );
};

export default AppDropZone;
