import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField";
import React, { useState } from "react";
import AppButton from "../../Common/AppButton";
import { RadioGroup, FormControlLabel, Radio, Grid } from "@material-ui/core";
import { AddPayoutMethodValidation } from "../../../validations";
import { getValidationErrors } from "../../../../utills/helpers";
import { useDispatch, useSelector } from "react-redux";
import { requestAddPayoutMethods } from "../../../../redux/actions";
import { IRootState } from "../../../../interfaces";
const DefaultErrors = {
  account_type: "",
  bank_account: {
    name: "",
    ifsc: "",
    account_number: "",
  },
  vpa: {
    address: "",
  },
};
const AddPayoutMethod = ({
  open,
  handleClose,
}: {
  open: boolean;
  handleClose: () => void;
}) => {
  const dispatch = useDispatch();
  const isLoading = useSelector(
    (state: IRootState) => state.paymentMethodsReducer.isAdding
  );
  const [formValue, setFormValues] = useState({
    account_type: "bank_account",
    bank_account: {
      name: "",
      ifsc: "",
      account_number: "",
    },
    vpa: {
      address: "",
    },
  });
  const [formError, setFormErrors] = useState(DefaultErrors);
  /**
   *
   * @param e
   */
  const accountTypeChanged = (e: any) => {
    const newFormValues = Object.assign({}, formValue);
    newFormValues.account_type = e.target.value;
    setFormValues(newFormValues);
    const newFormError = Object.assign({}, formError);
    newFormError.account_type = "";
    setFormErrors(newFormError);
  };
  /**
   *
   * @param e
   */
  const bankAccountDetailsChanged = (e: any) => {
    const { name, value } = e.target;
    const newFormValues = Object.assign({}, formValue);
    newFormValues.bank_account[
      name as "name" | "ifsc" | "account_number"
    ] = value;
    setFormValues(newFormValues);
    const newFormError = Object.assign({}, formError);
    newFormError.bank_account[name as "name" | "ifsc" | "account_number"] = "";
    setFormErrors(newFormError);
  };
  /**
   *
   * @param e
   */
  const UPIIDChange = (e: any) => {
    const newFormValues = Object.assign({}, formValue);
    newFormValues.vpa.address = e.target.value;
    setFormValues(newFormValues);
    const newFormError = Object.assign({}, formError);
    newFormError.vpa.address = "";
    setFormErrors(newFormError);
  };
  /**
   *
   */
  const addPaymentMethod = () => {
    try {
      AddPayoutMethodValidation.validateSync(formValue, {
        abortEarly: false,
      });
      if (formValue.account_type === "vpa") {
        delete formValue.bank_account;
      } else {
        delete formValue.vpa;
      }
      dispatch(requestAddPayoutMethods(formValue));
    } catch (error) {
      setFormErrors({ ...DefaultErrors, ...getValidationErrors(error) });
    }
  };
  return (
    <Dialog
      open={open}
      onClose={handleClose}
      aria-labelledby="form-dialog-title"
    >
      <form className={"form-container"}>
        <DialogTitle id="form-dialog-title">
          Add New Withdrawal (Payout) Method
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            To subscribe to this website, please enter your email address here.
            We will send updates occasionally.
          </DialogContentText>
          <>
            <RadioGroup
              row
              name="account_type"
              value={formValue.account_type}
              onChange={accountTypeChanged}
            >
              <FormControlLabel
                value="bank_account"
                control={<Radio value="bank_account" color="primary" />}
                label="Bank Account"
                labelPlacement="end"
              />
              <FormControlLabel
                value="vpa"
                control={<Radio value="vpa" color="primary" />}
                label="UPI"
                labelPlacement="end"
              />
            </RadioGroup>
            <br />
            {formValue.account_type === "vpa" ? (
              <>
                <TextField
                  label="Enter UPI ID"
                  type="text"
                  fullWidth
                  variant="outlined"
                  onChange={UPIIDChange}
                  name="vpaAddress"
                  value={formValue.vpa.address}
                  error={!!formError.vpa.address}
                  helperText={formError.vpa.address}
                />
              </>
            ) : (
              <>
                <Grid container spacing={2}>
                  <Grid item sm={6} xs={12}>
                    <TextField
                      label="Bank Name"
                      type="text"
                      fullWidth
                      variant="outlined"
                      onChange={bankAccountDetailsChanged}
                      name="name"
                      value={formValue.bank_account.name}
                      error={!!formError.bank_account.name}
                      helperText={formError.bank_account.name}
                    />
                  </Grid>
                  <Grid item sm={6} xs={12}>
                    <TextField
                      label="IFSC code"
                      type="text"
                      fullWidth
                      variant="outlined"
                      onChange={bankAccountDetailsChanged}
                      name="ifsc"
                      value={formValue.bank_account.ifsc}
                      error={!!formError.bank_account.ifsc}
                      helperText={formError.bank_account.ifsc}
                    />
                  </Grid>
                  <Grid item sm={12} xs={12}>
                    <TextField
                      label="Account Number"
                      type="number"
                      fullWidth
                      variant="outlined"
                      onChange={bankAccountDetailsChanged}
                      name="account_number"
                      value={formValue.bank_account.account_number}
                      error={!!formError.bank_account.account_number}
                      helperText={formError.bank_account.account_number}
                    />
                  </Grid>
                </Grid>{" "}
              </>
            )}
          </>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <AppButton
            onClick={addPaymentMethod}
            color="primary"
            text={"Add Method"}
            isLoading={isLoading}
            className="btn-primary "
          />
        </DialogActions>
      </form>
    </Dialog>
  );
};

export default AddPayoutMethod;
