import {
  Card,
  CardContent,
  CardHeader,
  Divider,
  Grid,
  Chip,
  makeStyles,
  Tooltip,
  CardActions,
} from "@material-ui/core";
import DateFnsUtils from "@date-io/date-fns";
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from "@material-ui/pickers";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import HelpIcon from "@material-ui/icons/Help";
import { IRootState } from "../../../../interfaces";
import {
  requestIdentityVerfication,
  getIdentityDocument,
  toggleConfirmBox,
} from "../../../../redux/actions";
import AppDropZone from "../../Common/AppDropZone";
import AppSelect from "../../Common/AppSelect";
import { IdentityVerificationValidation } from "../../../validations/IdentityVerfication";
import { getValidationErrors } from "../../../../utills/helpers";
import AppButton from "../../Common/AppButton";
import Loader from "../../Common/Loader";

const typesOfDocument: any[] = [
  { label: "Aadhar Card", value: "Aadhar Card" },
  { label: "Voter ID Card", value: "Voter ID Card" },
  { label: "Driving license", value: "Driving license" },
  { label: "PAN Card", value: "PAN Card" },
  {
    label: "Other Government Approved ID",
    value: "Other Government Approved ID",
  },
];

export const IdentityVerification = () => {
  const useStyles = makeStyles({
    heading: {
      display: "flex",
      alignItems: "center",
    },
    infoIcon: {
      display: "inline-flex",
      marginLeft: 5,
    },
    backimageContainer: {
      marginTop: 20,
    },
    success: {
      backgroundColor: "#28a745",
    },
    danger: {
      backgroundColor: "#dc3545",
    },
  });
  const [inputs, setInputs] = useState<any>({
    type: "",
    frontImage: null,
    frontImagePreview: "",
    backImage: null,
    backImagePreview: "",
    expirationDate: null,
  });
  const [formErrors, setFormErrors] = useState<any>({
    type: "",
  });
  const dispatch = useDispatch();
  const {
    isAdding,
    isLoading,
    data = {},
  } = useSelector((state: IRootState) => state.identityVerificationReducer);
  useEffect(() => {
    dispatch(getIdentityDocument());
    // eslint-disable-next-line
  }, []);
  useEffect(() => {
    if (data && data._id) {
      setInputs({
        type: data.type,
        frontImagePreview: data.frontImage
          ? `${process.env.REACT_APP_POST_IMAGE_ENDPOINT}${data.frontImage}`
          : "",
        backImagePreview: data.backImage
          ? `${process.env.REACT_APP_POST_IMAGE_ENDPOINT}${data.backImage}`
          : "",
        expirationDate: data.expirationDate,
      });
    }
    // eslint-disable-next-line
  }, [isLoading]);
  const handleDateChange = (date: any) => {
    setInputs((prevInputs: any) => ({
      ...prevInputs,
      expirationDate: date,
    }));
  };
  const formValueChange = (e: any) => {
    const name = e.target.name,
      value = e.target.value;
    setInputs((prevInputs: any) => ({
      ...prevInputs,
      [name]: value,
    }));
    setFormErrors((prevFormError: any) => ({
      ...prevFormError,
      [name]: "",
    }));
  };
  const onDrop = (acceptedFiles: any[], fieldName: string) => {
    acceptedFiles.forEach((file) => {
      const reader = new FileReader();
      reader.onload = (e) => {
        setInputs((prevInputs: any) => ({
          ...prevInputs,
          [`${fieldName}`]: acceptedFiles[0],
          [`${fieldName}Preview`]: e.target?.result,
        }));
        setFormErrors((prevFormError: any) => ({
          ...prevFormError,
          [`${fieldName}Preview`]: "",
        }));
      };
      reader.readAsDataURL(file);
    });
  };
  const onDelete = (fieldName: any) => {
    setInputs((prevInputs: any) => ({
      ...prevInputs,
      [`${fieldName}`]: null,
      [`${fieldName}Preview`]: "",
    }));
  };
  const handleSubmit = (e: any) => {
    e.preventDefault();
    try {
      IdentityVerificationValidation.validateSync(inputs, {
        abortEarly: false,
      });
      let payload = {
        ...inputs,
        action: data.status ? "update" : "",
        backImageRemoved: !inputs.backImagePreview,
      };
      delete payload.frontImagePreview;
      delete payload.backImagePreview;
      if (data.status === "approved") {
        dispatch(
          toggleConfirmBox({
            show: true,
            title: "Are you sure?",
            text: "If you update the documents, your documents will be resent for verification, and your account will be un-approved while we review it",
            onConfirm: () => {
              dispatch(requestIdentityVerfication(payload));
            },
          })
        );
      } else {
        dispatch(requestIdentityVerfication(payload));
      }
    } catch (error) {
      setFormErrors(getValidationErrors(error));
    }
  };
  const getStatusButton = (classes: any) => {
    let color: any = "default",
      label,
      className = "";
    switch (data.status) {
      case "pending":
        color = "primary";
        label = "In Review";
        break;
      case "approved":
        color = "success";
        label = "Approved";
        className = "success";
        break;
      case "rejected":
        color = "primary";
        label = "Rejected";
        className = "danger";
        break;
      default:
        color = "default";
        label = "Not requested";
        break;
    }

    return data.status === "rejected" ? (
      <Tooltip title={data.note}>
        <Chip
          className={classes[className]}
          label={label}
          color={color}
          size={"small"}
        />
      </Tooltip>
    ) : (
      <Chip
        className={classes[className]}
        label={label}
        color={color}
        size={"small"}
      />
    );
  };
  const {
    type = "",
    frontImagePreview = "",
    backImagePreview = "",
    expirationDate = null,
  } = inputs;
  const classes: any = useStyles();

  return (
    <>
      <Card>
        <CardHeader
          title={
            <Grid container>
              <Grid item sm={9}>
                <div className={classes.heading}>
                  <span>Apply for Identify Verification</span>
                  <span className={classes.infoIcon}>
                    <Tooltip title="You can upload any government approved photo ID card. E.g. Voting ID or Aadhar Card">
                      <HelpIcon />
                    </Tooltip>
                  </span>
                </div>
              </Grid>
              <Grid item sm={3} className="text-right">
                <span>{!isLoading && getStatusButton(classes)}</span>
              </Grid>
            </Grid>
          }
        />
        <Divider />
        <CardContent>
          {isLoading ? (
            <Loader />
          ) : (
            <Grid item sm={12}>
              <AppSelect
                label={"Type of ID *"}
                options={typesOfDocument}
                name={"type"}
                value={type}
                onChange={formValueChange}
                error={!!formErrors.type}
                helperText={formErrors.type}
                disabled={data.status === "pending"}
              />
              <AppDropZone
                label={"Upload front Image *"}
                message={
                  frontImagePreview ? (
                    <>
                      <img src={frontImagePreview} alt="" />
                    </>
                  ) : (
                    "Choose image"
                  )
                }
                accept="image/*"
                disabled={data.status === "pending"}
                error={!!formErrors.frontImagePreview}
                helperText={formErrors.frontImagePreview}
                onDrop={(files: any) => onDrop(files, "frontImage")}
                onDelete={() => onDelete("frontImage")}
              />
              <div className={classes.backimageContainer} />
              <AppDropZone
                label={"Upload Back Image(If applicable)"}
                message={
                  backImagePreview ? (
                    <>
                      <img src={backImagePreview} alt="" />
                    </>
                  ) : (
                    "Choose image"
                  )
                }
                accept="image/*"
                disabled={data.status === "pending"}
                isClearable={backImagePreview ? true : false}
                onDrop={(files: any) => onDrop(files, "backImage")}
                onDelete={() => onDelete("backImage")}
              />
              <MuiPickersUtilsProvider utils={DateFnsUtils}>
                <KeyboardDatePicker
                  autoOk={true}
                  className={"w-full"}
                  disableToolbar
                  variant="inline"
                  format="dd/MM/yyyy"
                  margin="normal"
                  id="date-picker-inline"
                  label="Expiration Date(If applicable)"
                  value={expirationDate}
                  onChange={handleDateChange}
                  KeyboardButtonProps={{
                    "aria-label": "change date",
                  }}
                  minDate={new Date()}
                  disabled={data.status === "pending"}
                />
              </MuiPickersUtilsProvider>
            </Grid>
          )}
        </CardContent>
        <Divider />
        <CardActions className={"pull-right"}>
          <AppButton
            onClick={handleSubmit}
            type="submit"
            text="Request"
            color="primary"
            isLoading={isAdding}
            className="btn-primary"
          />
        </CardActions>
      </Card>
    </>
  );
};
